@import "./assets/fonts/iconfont.css";
@import "./mediaqueries.scss";
@import "./theme.scss";

#page {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;

  .page-content {
    height: 100%;
    padding: 70px 0 0 0;
  }

  .page-content-main {
    height: 100%;
    overflow: hidden;
    background: rgba(243, 245, 247, 1);
  }

  .ant-modal-body {
    padding: 0;
  }

  .icon-xuanzhong1 {
    color: $icon;
  }
}

