#app {
  position: relative;
  height: 100%;

  .course {
    position: absolute;
    background-color: #62B1E6;
    top: 70px;
    left: 0;
    right: 0;
    height: 22px;
    line-height: 22px;
    z-index: 99;
    text-align: center;

    .course-inner {
      display: inline-block;
      color: #fff;

      .click {
        color: $bg-color;
        cursor: pointer;
      }
    }

    .close {
      float: right;
      margin-right: 10px;
      cursor: pointer;
    }
  }

  .noPc{
    &:hover{
      border: 2px solid #fff;
    }
  }
}

.module-canvas {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.2);
  z-index: 99;
  text-align: center;

  &::after {
    content: "";
    display: inline-block;
    height: 100%;
    vertical-align: middle;
  }
}

.ant-dropdown-menu-item {
  position: relative;
}

