.Tutorial {
  display: flex;
  justify-content: space-around;

  p {
    font-size: 18px;
    margin-bottom: 10px;
  }
}

.icon-check-s {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  visibility: hidden;
  top: 5px;
  right: 5px;
  width: 19px;
  height: 19px;
  background: rgba(255, 255, 255, 1);
  border-radius: 3px;
  z-index: 1;
  border: 1px solid $icon;

  i {
    font-size: 20px;
    color: $icon;
  }
}


